.background1 {
  background-image: url("../../images/plus.svg");
  background-color: transparent;
  width: 23px;
  height: 23px;
  border: none;
  background-size: cover;

}

.background2 {
  background-image: url("../../images/minus.svg");

  background-color: transparent;
  width: 23px;
  height: 23px;
  border: none;
  background-size: contain;
  background-repeat: no-repeat;
}
.ship-list {
  display: flex;
  flex-direction: column-reverse;
  justify-content: space-between;
  align-items: flex-start;
  margin: 20px;
  margin-left: 35px;
}

.ship-list__ships {
  width: 70%;

}

.ship-list__ship {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #ccc;
  padding: 10px;
}

.ship-list__ship span {
  margin-right: 10px;
  width: 30%;
}

.ship-list__details {
  margin-top: 10px;
  font-size: 13px;
  width: 40%;
}

.ship-list__details a {
  color: blue;
}

.ship-list__alphabet {
  display: flex;
  flex-direction: row;
}

.ship-list__alphabet button {
  margin: 5px;

  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.ship-list__alphabet button:hover {
  background-color: #e0e0e0;
}
/* Adress.module.css */

/* Стили для формы поиска */
.form {
  margin-top: 20px; /* Регулирует отступ сверху */
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  width: 90%;
  column-gap: 10px;
  align-items: center;
  margin-bottom: 30px;
}
.form span{
  width: 240px;
  display: block;
}
.form label {

  margin-bottom: 5px; /* Отступ снизу */
}
.adress__info{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.form input[type="text"] {
  width: 100%; /* Занимает всю доступную ширину */
  padding: 10px; /* Внутренние отступы */
  border: 1px solid #ccc; /* Граница поля */
  border-radius: 5px; /* Скругление углов */
  font-size: 16px; /* Размер шрифта */
}

.form input[type="text"]:focus {
  outline: none; /* Убирает рамку фокуса */
  border-color: #007bff; /* Цвет границы при фокусе */
}
@media screen and (max-width: 915px) {
  .ship-list__ships{
    width: 100%;
  }
  .form{
    width: 100%;
  }
}
@media screen and (max-width: 659px) {
  .ship-list__alphabet{
    display: none;
  }
}
@media screen and (max-width: 629px) {
  .adress__info{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;

  }
  .ship-list__details{
    width: 100%;
  }
}
@media screen and (max-width: 629px) {
  .ship-list{
    margin: 10px;
  }
  .form span{
    width: 170px;
    font-size: 15px;
  }
  .form input[type="text"]{
    font-size: 14px;
  }
}
@media screen and (max-width: 481px) {
  .form{
    flex-direction: column;
    align-items: flex-start;
    row-gap: 7px;
  }
  .form span{
    width: 240px;
    font-size: 15px;
  }
  .form input[type="text"]{
    font-size: 14px;
    width: 95%;
  }
}